<template>
  <div>
	<mt-header fixed title="登录"></mt-header>
	<div class="mt-content">
		<form id='login-form'>
			<div class="mui-input-row">
				<label>账 号</label>
				<input id='account' type="text" class="mui-input-clear mui-input" placeholder="请输入账号">
			</div>
			<div class="mui-input-row">
				<label>密 码</label>
				<input id='password' type="password" class="mui-input-clear mui-input" placeholder="请输入密码">
			</div>
			<div class="mint-cell-wrapper"><div class="mint-cell-title"><!----> <span class="mint-cell-text">自动登录</span> <!----></div> <div class="mint-cell-value"><label class="mint-switch"><input type="checkbox" class="mint-switch-input"> <span class="mint-switch-core"></span> <div class="mint-switch-label"></div></label></div> <!----></div>
			<mt-button @click="toLogin" class="btn-login" type="primary">登录</mt-button>
		</form>
		
	</div>
  </div>
</template>
<style scoped>
	input { 
	   border: none;
	   outline: none;
	   -webkit-appearance: none;
	}
	.mt-content {
		margin-top: 40px;
		padding: 16px;
	}
	.mui-input-row {
		width: 100%;
		display: flex;
		margin-top: 10px;
		font-size: 17px;
	}
	.mui-input-row label {
		display: block;
		padding-left: 3px;
		width: 50px;
		height: 36px;
		line-height: 36px;
	}
	.mui-input-row input {
		flex: 1;
		font-size: 15px;
		padding-left: 8px;
		border-radius: 3px;
		border: 1px solid #cccccc;
	}
	.btn-login {
		width: 100%;
	}
	.mint-cell-wrapper {
		padding: 10px 2px
	}
	.mint-header.is-fixed {
		background-color: #cd2525;
		font-size: 17px;
	}
		
</style>
<script>
 

  export default {
    data(){
      return {
		
      }
    },
    methods: {
		toLogin(e){
			e.preventDefault();
		}
    },
    mounted(){

    },
    created(){
      
    }
  }
</script>



